import React from 'react';
import TopHeader from './TopHeader';
import Menu from './Menu';
import GameBoard from './GameBoard';

class App extends React.Component {
    state = { 
        status: 1,
        currentPage: null
    };

    componentDidMount() {
        this.SwitchMainUI();
    }

    SwitchMainUI = (option = 1) => {
        console.log("Switching UI: ");
        console.log("Option: "+option);

        if(option) {
            this.setState({status: option}, () => {
                console.log("Status: "+this.state.status);
                switch(this.state.status) {
                    case 2: {
                        console.log("Gameboard Element");
                        setTimeout(() => {
                            this.setState({currentPage : <div style={{display: "flex", justifyContent: "center", paddingTop: "5%", alignItems: "center"}}><GameBoard size = {10} images = {this.state.images} /></div>})
                          }, 1500);
                        break;
                    }
                    default:
                        console.log("Menu element");
                        this.setState({currentPage : <div style={{display: "flex", justifyContent: "center", paddingTop: "5%", alignItems: "center"}}><Menu  onGameStart = {this.SwitchMainUI}/></div>});
                        break;
                }
            });
        }
       
        
        
    }
    /*get this inside callback from clicking start.
    switch(this.state.status) {
            case 1: {
                this.setState({currentPage : <div style={{display: "flex", justifyContent: "center", paddingTop: "5%", alignItems: "center"}}><Menu/></div>});
                break;
            }
            case 2: {
                this.setState({currentPage : <div style={{display: "flex", justifyContent: "center", paddingTop: "5%", alignItems: "center"}}><GameBoard/></div>});
                break;
            }
            default:
                break;
        }
    */

    render() {
        return (
            <div style={{backgroundColor: "#ecf0f1" ,width: "100%",height: "100%"}}>
                <div style={{paddingTop: "10px",paddingBottom: "10px",backgroundColor: "#ecf0f1"}}>
                    <TopHeader/>
                </div>
                {this.state.currentPage}
            </div>
        );
    }
}

export default App;