import React from 'react';
import unsplash from '../api/unsplash';

class GameBoard extends React.Component {
    state = {
        images: {
            houses: [],
            dogs: [],
            cars: [],
            nature: [],
            buildings: []
        },
        Board: {
            Size: this.props.size,
            Cards: [{id: "",key: ""}]
        }
    }

    
    componentDidMount() {
        console.log("DidMount");
        this.onLoadInitialize();
    }

    onLoadInitialize = async () => {
        console.log("OnLoadInitialize");
        let i_houses = await unsplash.get('/search/photos', {
            params: { query: 'house' }
        });
        const i_cars = await unsplash.get('/search/photos', {
            params: { query: 'car' }
        });

        let i_dogs = await unsplash.get('/search/photos', {
            params: { query: 'dog' }
        });

        let i_nature = await unsplash.get('/search/photos', {
            params: { query: 'nature' }
        });

        let i_buildings = await unsplash.get('/search/photos', {
            params: { query: 'building' }
        });

        let prev = Object.assign({},this.state.images);
        prev.buildings = i_buildings.data.results;
        prev.cars = i_cars.data.results;
        prev.houses =  i_houses.data.results;
        prev.dogs = i_dogs.data.results;
        prev.nature = i_nature.data.results;

        this.setState({images: prev});

        console.log(this.state.images.cars);

        /*this.setState(images => {  
            let prev = Object.assign({},this.state.images);
            prev.buildings = i_buildings.data.results;
            prev.cars = i_cars.data.results;
            prev.houses =  i_houses.data.results;
            prev.dogs = i_dogs.data.results;
            prev.nature = i_nature.data.results;
            return {prev};
           }
        );*/
        
        

        //this.setState({number: Math.random()}); //generate random number
        //to take random pics for the game each load.
    }

    render() {
        return <div>Game board {console.log(this.state.Board.Cards)}</div>;
    }
}

export default GameBoard;