import React from 'react';
import { Collapse } from 'react-collapse';


class Menu extends React.Component {
    state = {startBtn: "ui button huge", isopen: true};

    loadGame = () => {
        console.log("in LoadGame");
        this.setState({startBtn: "ui button loading huge disabled", isopen: !this.state.isopen});
        this.props.onGameStart(2);
    }


    render() {
        return (
            <div className="ui vertical buttons" style={{width: "50%"}}>
                <button className={this.state.startBtn} onClick={this.loadGame}>New Game</button>
                {console.log(this.state.isopen)}
                <Collapse isOpened = {this.state.isopen}>
                    <div>
                            <button className="ui button huge" style={{marginTop: "10px"}}>Records</button>
                            <button className="ui button huge" style={{marginTop: "10px"}}>About</button>
                    </div>
                </Collapse>
            </div>
        );
    }
    
    
}

export default Menu;